.pagination-ui-component {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    width: 100%;
  }
  
  .pagination-ui-component button {
    cursor: pointer;
    border: none;
    padding: 10px;
    margin-right: 10px;
    margin-left: 10px;
    width: 100px;
    transition: background-color 0.3s ease;
    font-weight: 500;
    font-size: 16px;
    box-shadow: inset 0 0 0 1px #ccc;
    background-color: var(--background-color);
    color: var(--text-color);
  }
  
  .pagination-ui-component button:hover {
    background-color: #007bff;
  }
  
  .pagination-ui-component button:active {
    background-color: var(--border-color);
  }